<script>
import tagDialog from "./TagDialog"
import PostApi from "../../apis/PostApi"

import { mapState, mapActions } from 'vuex'

export default {
  name: 'Post',
  components:{
    tagDialog,
  },
  computed:{
    ...mapState({
      userInfo: state => state.user.userInfo,
      myfollowing: state => state.tags.followtags
    }),
  },
  data() {
    return {
      /*  /  */
      content_show: false,
      comment_show: false,
      tagDialog_show: false,
      dialogTagName:'',
      /*  */
      selection: 1,
      loading:true,
      /*    */
      postdata_one_line : "  ",
      postdata_full : "      ",
      postdata_date : "",
      tag_list : [],
      tag:'',
      /*  */
      commentArea:'',
      isWriting:-99,
      commentHolder:' ',
      anonymous: 0,
      like: this.post.like_check,
      cnt: this.post.like_count,
      // bookmark:false,
      bookmark: this.post.bookmark_check,
      imageDialog:false,
    }
  },
  methods: {
    ...mapActions({
      getAllTab: 'tags/getAllTab',
      getMyfollowing: 'tags/getMyfollowing',
    }),
    vote(choice){
      var temp = ''
      if (choice === 1){
        temp = 'A'
      }else{
        temp = 'B'
      }
      console.log(choice,' .')
      PostApi.registerVote({vno:this.post.vote.vno, choice:choice},res=>{
        if(res.data.state === 'ok'){
          this.post.vote.my_value = temp
        }
      },err=>{console.log(err)})
    },
    togglebookmark(){
      this.bookmark = !this.bookmark
      if(!this.bookmark){
        PostApi.deleteOneBookMark({pno:this.post.pno},res=>{
          if (res.data.state === 'ok'){
          console.log(res)
          }else{
          console.log(res)
          }
        },err=>{
          console.log(err)
        })
      }else{
        PostApi.updateBookMark({pno:this.post.pno},res=>{
          if (res.data.state === 'ok'){
          console.log(res)
          }else{
          console.log(res)
          }
        },err=>{
          console.log(err)
        })
      }
    },
    liketoggle(){
      // console.log(this.cnt, this.like)
      if (this.like){
        this.cnt -= 1
      }else{
        this.cnt += 1
      }
      this.like = !this.like
      PostApi.like({pno: this.post.pno},res=>{
        if(res.data.state==='ok'){
          console.log(res.data)
        }
      },err=>{
        console.log(err)
      })
    },
    closedialog(){
      this.tagDialog_show = false
    },
    deleteComment(no, idx){
      // this.post.comment[idx].user.id = ''
      PostApi.deleteComment(
        {no:no},res=>{
          if (res.data.message === '  '){
            // console.log(res)
            console.log('   .')
            this.post.comment[idx].content = '  .'
          }
        },err=>{
          console.log(err)
        }
      )
    },
    clickTag(tagName) {  //    
      console.log(">> : ",tagName)
      this.dialogTagName = tagName
      this.tagDialog_show = true;
      console.log(' ',this.myfollowing)
    },
    setContent(){
      // 
      var line_max = 25 //  (25  ?)
      //console.log('setContent()')
      this.postdata_full = this.post.content.replace(/\n/gi,'<br/>');// !!
      this.postdata_one_line = this.post.content.substring(0,line_max) + '...'
    },
    makeTagList(){
      this.tag_list = this.post.hashtag.replace(' ','').split('#')
      this.tag_list.splice(0,1)
    },
    getCommentList(){
        PostApi.getComment({pno:this.post.pno},
          res=>{
            if(res.status===200){
              this.post.comment = res.data
            }else{
              console.log(res,'  ')
            }
          },err=>{  
              console.log(err,'  ')
        })
    },
    commentEnter(){
      /*  
      1.   returnBody  ..
      2.    (this.post.comment  )
      3.   
      */

      var returnBody = 
      {
        "anonymous": this.anonymous,
        "content": this.commentArea,
        // "id": '', //id..
        // "nickname": '', // ..
        "parent": this.isWriting==-99?'':this.isWriting, //    ..
        "pno": this.post.pno
      }
      PostApi.requestComment(returnBody, 
      res=>{
        if (res.data.state==="ok"){
          this.getCommentList()
        }else{
          console.log(res.data, '  ')
        }
      },err=>{
          console.log(err, '  ')
      })
      this.commentArea='' //  
    },
    subComment(cNo,cNicname){
      if(this.isWriting==-99){
        //
        this.isWriting = cNo
        this.commentHolder = cNicname + ' '
      }else{
        //
        this.isWriting=-99
        this.commentHolder = ' '
      }
    },


    //  
    transferTime(time){    
     var now = new Date();
     var sYear = time.substring(0,4);
     var sMonth = time.substring(5,7)-1;
     var sDate = time.substring(8,10);
     var sHour = time.substring(11,13);
     var sMin = time.substring(14,16);
     var sSecond = time.substring(17,19);
     var sc = 1000;
 
     var today = new Date(sYear,sMonth,sDate,sHour,sMin,sSecond);
     // 
     var pastSecond = parseInt((now-today)/sc,10);
    //  console.log(":"+pastSecond)
       var str = "";
    
    if(pastSecond<60){
      str=' '
    }else if(pastSecond<3600){
      str = parseInt((pastSecond/60),10)+' '
    }else if(pastSecond<86400){
      str = parseInt((pastSecond/3600),10)+' '
    }else if(pastSecond<2592000){
      str = parseInt((pastSecond/86400),10)+' ' //   ..
    }else{
      str = sYear+'/'+sMonth+'/'+sDate
    }


     
     return str;
    }
  },
  mounted:function () {
          /*   
    1.  (  )    => setDate()
    2.    
    3.          => setContent()
    4.  
    */
   //console.log(' : '+this.post.datetime)
   this.postdata_date = transferTime(this.post.datetime)
   this.setContent()
   this.makeTagList()
   this.loading = false;
   for(var i=0; i<this.post.comment.length; i++){
     this.post.comment.datetime = transferTime(this.post.comment.datetime)
   }

  },
  props:{
    post: {
      type: Object,
      required: true,
    },
  },
  
}
function transferTime(time){    
     var now = new Date();
     var sYear = time.substring(0,4);
     var sMonth = time.substring(5,7)-1;
     var sDate = time.substring(8,10);
     var sHour = time.substring(11,13);
     var sMin = time.substring(14,16);
     var sSecond = time.substring(17,19);
     var sc = 1000;
 
     var today = new Date(sYear,sMonth,sDate,sHour,sMin,sSecond);
     // 
     var pastSecond = parseInt((now-today)/sc,10);
    //  console.log(":"+pastSecond)
       var str = "";
    
    if(pastSecond<60){
      str=' '
    }else if(pastSecond<3600){
      str = parseInt((pastSecond/60),10)+' '
    }else if(pastSecond<86400){
      str = parseInt((pastSecond/3600),10)+' '
    }else if(pastSecond<2592000){
      str = parseInt((pastSecond/86400),10)+' '
    }else{
      str = sYear+'/'+sMonth+'/'+sDate
    }     
     return str;
}
</script>

